import modelExtend from 'dva-model-extend'
import { queryRegprofile } from '../../services/signup'
import { pageModel } from '../../models/common'
// import { message } from 'antd'
// import queryString from 'query-string'
// import { message } from 'antd'
interface Istate {
  regprofile: any
}

export default modelExtend(pageModel, {
  namespace: 'ycyh',
  state: {
    // currentItem: {},
    // modalVisible: false,
    // detailVisible: false,
    regprofile: {},
  },
  subscriptions: {
    setup ({ dispatch, history }: any) {
      history.listen((location: any) => {
        if (location.pathname === '/signup/ycyh') {
          dispatch({
            type: 'query',
            payload: {
              // status: 2,
            },
          })
        }
      })
    },
  },
  effects: {
    * query ({ payload }: any, { call, put }: any) {
      const data = yield call(queryRegprofile, payload)
      if (data.success) {
        const item = data.data
        yield put({
          type: 'updateState',
          payload: {
            regprofile: item.activity_signup.data[0],
          },
        })
      } else {
        throw data
      }
    },
  },
  reducers: {
    updateState (state: Istate, { payload }: any) {
      return {
        ...state,
        ...payload,
      }
    },
    // updateDetailState (state, { payload }) {
    //   // let detail = state.detail
    //   return {
    //     ...state,
    //     detail: {
    //       ...state.detail,
    //       ...payload,
    //     },
    //   }
    // },
    // updateUserState (state, { payload }) {
    //   // let detail = state.detail
    //   return {
    //     ...state,
    //     user: {
    //       ...state.user,
    //       ...payload,
    //     },
    //   }
    // },
  },
})



